<template>
    <div style="height:100%">
        <div :id="targetId"  style="height:100%" v-loading="isLoading"></div>
        <div :id="overlayContainerId" style="display: none;">
            <!-- <div id="marker" title="Marker">
                <img src="../../static/assets/images/loading.gif"/>
            </div> -->
            <!-- <img id="marker" src="../../static/assets/images/loading.gif"/> -->
        </div>
        <template v-for="(popupData,key,index) in popupDatas">
            <div :id="popupData.id+'-popup'" :ref="popupData.id+'-popup'" class="ol-popup" :key="index">
                <a href="#" :overlayid="popupData.id" :id="popupData.id+'-popup-closer'" class="ol-popup-closer" @click="closePopup($event)"></a>
                <div :id="popupData.id+'-popup-content'">
                    <span v-html="popupData.content"></span>
                </div>
            </div>
        </template>
        
        <template>
            <div v-show="showTrafficInfoPop" id="traffic-info-release-popup" class="ol-popup yk-pointer-normal" >

                <img class="yk-close-btn" src="static/images/close.png" @click="closeMyInfoWindow('self')">

                <div id="traffic-info-release-popup-content">                   

                    <div>
                        <el-row class="yk-pad-10 yk-bottom-border">
                            <label class="yk-info-window-title"></label>                        
                        </el-row>
                        <el-row class="yk-pad-1020">
                            
                            <el-form ref="ruleFormMap" :rules="rules" :model="trafficInfo" size="mini" label-width="108px" class="demo-ruleForm yk-left" style="margin-right: 10px;">

                                <el-form-item label="" class="yk-bottom-12 yk-txt">
                                    <span>{{trafficInfo.eventName}}</span>
                                </el-form-item>

                                <el-form-item label="" prop="name" class="yk-bottom-12 yk-txt">                                
                                    <span>{{trafficInfo.longitude + ',' + trafficInfo.latitude}}</span>
                                </el-form-item>

                                <el-form-item label="" prop="affectRange" class="yk-bottom-12 yk-txt" style="height: 45px;">
                                    <el-slider style="padding: 0px 5px;" :show-tooltip="slideFlag" v-model="trafficInfo.affectRange" :marks="broadcastRangeMarks" :min="broadcastMin" :max="broadcastMax" :step="broadcastStep"  @change="sliderChange"></el-slider>
                                </el-form-item>                                

                                <el-form-item label="" prop="alertPath" class="yk-bottom-12 yk-txt">

                                    <!-- placeholder="1.1,2.2;3.3,4.4" -->
                                    <el-input size="mini" v-model="select.alertPath" class="yk-readonly" style="margin-right: 50px;">
                                        <template slot="append">
                                            <el-button class="yk-btn-append" type="primary" @click="addEffectPath();">
                                                <template v-if="!select.alertPath"></template>
                                                <template v-else></template>
                                            </el-button>
                                        </template>
                                    </el-input>

                                    <el-tooltip class="yk-btn-tooltip" effect="light" :content="select.alertPath" placement="top-end" popper-class="path-view-popover" v-if="select.alertPath">
                                       <el-button class="yk-btn-append" type="primary"></el-button>
                                    </el-tooltip>
                                </el-form-item>

                                <!-- <el-form-item label="" prop="alertRadius" class="yk-bottom-12 yk-txt">
                                    <el-input size="mini" v-model="trafficInfo.alertRadius">
                                        <span slot="append" class="yk-unit"></span>
                                    </el-input>
                                </el-form-item> -->
                                <!-- <el-input-number v-model.trim="ruleForm.alertRadius" controls-position="right" :min="1" :max="1024"></el-input-number> -->
                                <el-form-item label="" prop="alertRadius" class="yk-bottom-12 yk-txt">
                                    <el-input-number v-model.trim="trafficInfo.alertRadius" controls-position="right" :min="1" :max="1024"></el-input-number>
                                    <span class="c-ml-10">:10cm</span>
                                </el-form-item>

                                <el-form-item label="" prop="content" class="yk-bottom-16 yk-textarea">
                                    <el-input type="textarea" size="mini" v-model="trafficInfo.content"></el-input>
                                </el-form-item>

                                <el-form-item label="" prop="frequency" class="yk-bottom-12 yk-txt">
                                    <el-input size="mini" v-model="trafficInfo.frequency">

                                        <template slot="append">
                                            <select class="yk-w-51 yk-input-select-2" v-model="select.frequencyUnit">
                                                <option v-for="(item,index) in frequencyUnitList" :key="index" :value="item">{{item.name}}</option>
                                            </select>
                                        </template>

                                    </el-input>
                                </el-form-item>
                                
                                <el-form-item label="" prop="beginTime" class="yk-bottom-12 yk-txt">
                                    <el-date-picker
                                        v-model="trafficInfo.beginTime"
                                        type="datetime"
                                        placeholder="">
                                    </el-date-picker>
                                </el-form-item>

                                <el-form-item label="" prop="endTime" class="yk-bottom-12 yk-txt">
                                    <el-date-picker
                                        v-model="trafficInfo.endTime"
                                        type="datetime"
                                        placeholder="">
                                    </el-date-picker>
                                </el-form-item>

                                <el-form-item v-if="trafficInfo.isEdit" prop="datasource" label="" class="yk-bottom-12  yk-txt">                                    
                                    <select class="yk-select" v-model="select.datasource">
                                        <option v-for="(item,index) in datasourceList" :key="index" :value="item">{{item.name}}</option>                                        
                                    </select>
                                </el-form-item>

                                <el-form-item label="" class="yk-bottom-12  yk-txt" v-if="false">                                    
                                <!-- <el-form-item label="" class="yk-bottom-12  yk-txt">                                     -->
                                    <el-input size="mini" v-model="trafficInfo.alertCategory"></el-input>
                                </el-form-item>

                                <el-form-item style="text-align:right;margin-top: 10px;margin-top: 15px;margin-bottom: 10px;">
                                    <el-button class="yk-w-80 yk-border-normal" type="warning" :loading="publishLoading" v-show="!trafficInfo.isEdit" @click="publichInfo($event);"></el-button>
                                    <el-button class="yk-w-80 yk-border-normal" type="info" v-show="!trafficInfo.isEdit" @click="closeInforWindow($event);"></el-button>

                                    <el-button class="yk-w-80 yk-border-normal" type="warning" :loading="updateLoading" v-show="trafficInfo.isEdit" @click="updateInfo($event);"></el-button>
                                    <el-button class="yk-border-normal" type="info" :loading="invalidLoading" v-show="trafficInfo.isEdit" @click="destroyInfo($event);"></el-button>                                    
                                </el-form-item>

                            </el-form>
                        </el-row>
                    </div>
                </div>
            </div>
        </template>
    </div>
</template>
<script>

// import startIcon from '@/assets/images/start.png';
// import endIcon from '@/assets/images/end.png';
// import startIcon2 from '@/assets/images/start2.png';
// import endIcon2 from '@/assets/images/end2.png';

import _ from 'lodash';
import "ol/ol.css";
import { unByKey } from 'ol/Observable.js'
// import OLObservable from 'ol/Observable.js'
import Map from 'ol/Map.js';
import View from 'ol/View.js';
import Overlay from 'ol/Overlay.js';
import {Image as ImageLayer} from 'ol/layer.js';
import ImageWMS from 'ol/source/ImageWMS.js';
import {Circle as CircleStyle, Fill, Stroke, Style} from 'ol/style.js';
import {defaults as defaultControls,ScaleLine,FullScreen} from 'ol/control.js';
import {defaults as defaultInteractions, DragRotateAndZoom} from 'ol/interaction.js';
import TileLayer from 'ol/layer/Tile.js';
import BingMaps from 'ol/source/BingMaps';
import OSM from 'ol/source/OSM.js';
import Polygon from 'ol/geom/Polygon';
import Point from 'ol/geom/Point';
import MousePosition from 'ol/control/MousePosition.js';
import * as mapInit from './MapUtils.js';

import TDate from '@/common/date.js'

import axios from 'axios'

export default {
    name:"TusvnMap",
    props:["targetId","overlayContainerId",], //'trafficInfo'
    data(){
        return {
            slideFlag:true,
            isLoading: false,
            pointerFlag:false,
            orderStatus: -1
            ,map: null
            ,project:"EPSG:4326"//
            // 120.80224989415075-----latitude:31.28000259048651
            ,center: [120.80224989415075,31.28000259048651]//
            ,zoom: 10//
            ,planLineTracks: []
            ,realLineTracks: []
            ,pageHeight: 400
            ,carLayerId:"carLayer"
            ,currentDivPolygon:null//div
            // ,startIcon: '@/assets/images/start.png'
            // ,endIcon: '@/assets/images/end.png'
            ,popupDatas:{}
            ,overlays:{},

            // 
            showTrafficInfoPop: false,
            ruleForm: {
                eventName: '',
                eventType: '',
                name: '',
                eventCategory: '',
                frequency: '',
                frequencyUnit: '',
                delivery: false,
                content: '',
                alertCategory: ''
            },
            rules: {                
                content: [
                    { required: true, message: '', trigger: 'blur' },
                    // { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
                ],               
                alertPath: [
                    { required: true, message: '', trigger: 'blur' },
                    // { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
                ],
                frequency: [
                    { required: true, message: '', trigger: 'blur' },
                ],
                // beginTime: [
                //     { type: 'date', required: true, message: '', trigger: 'change' },
                // ],
                // endTime: [
                //     { type: 'date', required: true, message: '', trigger: 'change' },
                // ],
                datasource: [
                    { required: true, message: '', trigger: 'change' }
                ],
            },
            updateLoading: false,
            publishLoading: false,
            invalidLoading: false,
            trafficInfo : {
                id: '',
                title: '',
                isEdit: false,
                eventName: '',
                eventType: '',
                taskCode: '',                          
                longitude: '',
                latitude: '',
                // affectRange: 200,
                alertRadius: 1024,
                content: '',
                frequency: 500,
                frequencyUnit: '',
                beginTime: new Date().getTime(),
                endTime: new Date().getTime(),
                datasource: '',
                // alertRadius: 1000,
                affectRange: 1000,
                alertPath: '',              // "[[12.333,23.333],[12.444,23,444]]" 12.333,23.333;12.444,23,444
                icon: '',
                alertCategory: ''
            },
            frequencyUnitList: [],
            datasourceList: [],
            broadcastMin: 200,
            broadcastMax: 3000,
            broadcastStep: 100,
            broadcastRangeMarks: {
                200: '200',                                
                3000: '3000'
                // {
                //     style: {
                //         color: '#1989FA',
                //     },
                //     label: this.$createElement('strong', '3000')
                // },
            },
            clickEventKey: null,
            select: {
                datasource: '',
                frequencyUnit: {
                    name: '',
                    value: '',
                },
                sliderVal: 1000,
                alertPath: '',  // '12.3,23.3;12.3,23.3;'                
            },

            circleLon: '',
            circleLat: '',
            circleRangeID: 'circle_range_id',

            TempLayer: 'TempLayer',         // 
            TempIcoLayer: 'TempIcoLayer',
            tempElement: {
                closeBtnId: 'path_ico_close_btn',
                okBtnId: 'path_ico_ok_btn',
                markerId: 'marker_ico_green'
            },
            mapStatus: 'normal',        // 1normal   2TempLayerInteraction  
            pathPoint: {        // 
                newVal: {
                    lon: '',
                    lat: '',
                },
                oldVal: {
                    lon: '',
                    lat: '',
                },
                pointList: [],
            },
            pointData: null,
            MessageTempLayer: 'MessageTempLayer',
        }
    },
    watch:{
    },
    methods: {
        submitForm(formName='ruleFormMap') {
            let bool = false;
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    bool = true;
                } else {                  
                    bool = false;
                }
            });
            return bool;
        },
        resetForm(formName='ruleFormMap') {
            this.$refs[formName].resetFields();
        },
        
        // 
        sliderChange(value){
            this.slideFlag=false;
            setTimeout(() => {
                this.slideFlag=true;
            },300)
            this.select.sliderVal = value;
            this.trafficInfo.affectRange = value;
            // this.trafficInfo.alertRadius = value;

            this.drawBgCircle(this.circleLon,this.circleLat,value);
        },
        // 
        // 1   2       3    
        addEffectPath(){
            this.closeMyInfoWindow();    //  
            this.pathPoint.newVal.lon = '';
            this.pathPoint.newVal.lat = '';
            this.pathPoint.oldVal.lon = this.trafficInfo.longitude;
            this.pathPoint.oldVal.lat = this.trafficInfo.latitude;
            this.pathPoint.lineList = [];
            this.mapStatus = 'TempLayerInteraction';

            this.addClickEvent();     
            this.addPathIco( this.trafficInfo.longitude, this.trafficInfo.latitude );

            this.$emit('TemporaryClearPubMsg',{bool:true});
            this.pointerFlag=true;


            this.$emit("setPointer",{bool: true, flag: true}); 
            
        },

        //  
        addPathIcoBtn(lon,lat){

            // 
            let id = this.tempElement.okBtnId;
            let imgUrl = 'static/images/ok4.png';
            let courseAngle = null;
            let bdata = null;
            let offset = [50,0];
            let callback = null;
            this.addImgOverlay(id, imgUrl, courseAngle, lon, lat, bdata, offset, this.okClick);

            // 
            let id2 = this.tempElement.closeBtnId;
            let imgUrl2 = 'static/images/close4.png';
            let courseAngle2 = null;
            let bdata2 = null;
            let offset2 = [-50,0];
            let callback2 = null;
            this.addImgOverlay(id2, imgUrl2, courseAngle2, lon, lat, bdata2, offset2, this.closeClick);
        },

        // 
        okClick(e){
            if(this.pathPoint.pointList.length > 0) {
                const path = JSON.stringify(this.pathPoint.pointList);
                this.trafficInfo.alertPath = path;
                this.select.alertPath = path;
                this.pointData.trafficInfo.alertPath = JSON.stringify(this.pathPoint.pointList);
            }else {
                this.trafficInfo.alertPath = '';
                this.select.alertPath = '';
            }
            this.clearTempLayer();
            this.addMyInfoWindow(this.pointData, true);

            this.$emit('TemporaryClearPubMsg',{bool:false});

            this.$emit("setPointer",{bool: false, flag: true}); 

        },
        // 
        closeClick(e){

            this.$emit("setPointer",{bool: true, flag: true}); 
            this.deleteTempMarker();
            this.addClickEvent();            
            // e.preventDefault();
            // e.stopPropagation();
            // this.clearTempLayer();
        },
        deleteTempMarker(){
            this.removeOverlayById(this.tempElement.okBtnId);
            this.removeOverlayById(this.tempElement.closeBtnId);
            this.removeAllFeature(this.TempIcoLayer);
        },
        clearTempLayer(){
            this.removeAllFeature(this.TempLayer);
            this.removeAllFeature('vectorLayer_01');
            this.deleteTempMarker();

            this.mapStatus = 'normal';
            this.pathPoint.pathPoint = {        // 
                newVal: {
                    lon: '',
                    lat: '',
                },
                oldVal: {
                    lon: '',
                    lat: '',
                },
                list: [],
                pointList: [],
            };
        },
        
        // 
        addPathIco(lon,lat,type="normal"){            
            
            // id, imgUrl, courseAngle, lon, lat, bdata, offset, callback
            let id = this.tempElement.markerId;//'temp_' + (new Date()).getTime();
            let imgUrl = 'static/images/circle112.png';    // 
            let imgUrl2 = 'static/images/circle222.png';    // 
            let courseAngle = null;
            let bdata = null;
            let offset = [-32,-32];
            // let callback = this.tempClick();
            let layerId = type == 'normal' ? this.TempLayer : this.TempIcoLayer;;
            let size = [18,18];
            let rotation = null;
            let rotateWithView = null;
            let opacity = null;
            let scale = null;
            let anchor = null;

            let imgPath = type == 'normal' ? imgUrl : imgUrl2;
            
            // this.addImgOverlay(id, imgUrl, courseAngle, lon, lat, bdata, offset, callback);
            // lon,lat,id,layerId,carImgUrl,size,rotation,rotateWithView,opacity,offset,scale,anchor
            this.addImg(lon,lat,id,layerId,imgPath,size,null,null,null);

        },
        // 
        addPathLine(startLon,startLat,endLon,endLat){

            // 
            // // ===============================================

            //  
            let url = window.config.dlUrl + 'route/getRoadCoornatesByCoordinate.do';
            let params = {
                edgeTableName: 'dl_shcsq_wgs84_rc_withoutz',
                vertexTableName: 'dl_shcsq_wgs84_rc_withoutz_vertices_pgr',
                startX: startLon,
                startY: startLat,
                endX: endLon,
                endY: endLat,
            };

            let formData = new FormData(); //form
            formData.append('edgeTableName', 'dl_shcsq_wgs84_rc_withoutz');//appendform
            formData.append('vertexTableName', 'dl_shcsq_wgs84_rc_withoutz_vertices_pgr');//appendform
            formData.append('startX', startLon);//appendform
            formData.append('startY', startLat);//appendform
            formData.append('endX', endLon);//appendform
            formData.append('endY', endLat);//appendform

            let config = {
                headers: {
                    'Accept': '*/*',
                    'Content-Type': 'application/x-www-form-urlencoded'
                }
            }; //
            this.isLoading = true;
            axios.post(url, formData, config)
                .then(response => {
                    this.isLoading = false;
                    if(response.data.data) {
                        let pointList = response.data.data ? response.data.data : '';
                        if(pointList.length < 2) {
                            this.$message({
                                type: 'error',
                                duration: '1500',
                                message: "",
                                showClose: true
                            });
                            // this.addPathIcoBtn(startLon,startLat);
                            // this.$emit('TemporaryClearPubMsg',{bool:false});
                            return;
                        }
                        if(pointList.length > 32) {
                            this.$message({
                                type: 'error',
                                duration: '1500',
                                message: "",
                                showClose: true
                            });
                            // this.addPathIcoBtn(startLon,startLat);
                            // this.$emit('TemporaryClearPubMsg',{bool:false});
                            return;
                        }

                        let points = [];
                        for(let i=0;i<pointList.length;i++){
                            let temp = pointList[i];
                            points.push([temp.x,temp.y]);
                        }
                        // if(points){
                            this.pathPoint.pointList = this.pathPoint.pointList.concat(points);    
                        // }
                        // console.log(points);

                        // console.log(response.data.data);
                        // console.log(this.pathPoint.pointList);
                        let coordinates = points;
                        let id = 'TempLine_' + (new Date()).getTime();
                        let color = 'red';
                        let lineGap = 'round';
                        let lineJoin = 'round';
                        let lineDash = [0,5];
                        let lineDashOffset = 0;
                        let miterLimit = 10;
                        let width = 5;
                        let layerId = this.TempIcoLayer;
				
                        // coordinates, id, color, lineCap, lineJoin, lineDash, lineDashOffset, miterLimit, width, layerId
                        this.addLineString(coordinates, id, "red", "round", "round", [5,0], [-14,0], 10, 5, layerId);  
                    }else {
                        this.$message({
                            type: 'error',
                            duration: '1500',
                            message: response.data.message || "",
                            showClose: true
                        });
                            

                    }  

                    // 
                    this.addPathIcoBtn(startLon,startLat);
                    this.$emit("setPointer",{bool: false, flag: true}); 
                }).catch((error) => {
                    this.isLoading = false;
                    this.$message({
                        type: 'error',
                        duration: '1500',
                        message: "",
                        showClose: true
                    });
                    this.addPathIcoBtn(startLon,startLat);
                    this.$emit("setPointer",{bool: false, flag: true});
                }
            );
            
        },
        tempClick(){

        },
        hidePopWin(){

        },        

        // 
        converAlertPath(){
            
            if(this.select.alertPath && this.select.alertPath.indexOf(';')){
                let t = this.select.alertPath.split(';');
                let arr= [];

                for(let i=0;i<t.length;i++){
                    let m = t[i].split(',');
                    arr.push(m);
                }
                
                let str = JSON.stringify(arr);
                return str;
            }
            return '';
        },
        publichInfo(e){
            this.publishLoading = true;
            this.trafficInfo.datasource = this.select.datasource ? (this.select.datasource.key ? this.select.datasource.key : '') : '';
            this.trafficInfo.frequencyUnit = this.select.frequencyUnit ? (this.select.frequencyUnit.key ? this.select.frequencyUnit.key : '') : '';
            // console.log(this.trafficInfo.alertCategory);
            if(!this.submitForm()) return; 

            let url = 'event/task/save';
            let params = {
                status: 1,
                eventType: this.trafficInfo.eventType,      //
                longitude: this.trafficInfo.longitude,      // 
                latitude: this.trafficInfo.latitude,       // 
                affectRange: this.trafficInfo.affectRange,    // 
                content: this.trafficInfo.content,        // 
                frequency: this.trafficInfo.frequency,      // 
                frequencyUnit: this.trafficInfo.frequencyUnit,      // 
                beginTime: TDate.dateToMs(this.trafficInfo.beginTime),      // 
                endTime: TDate.dateToMs(this.trafficInfo.endTime),     // 
                datasource: this.trafficInfo.datasource,     // 
                sendChannel: this.trafficInfo.sendChannel,         //  4G
                infoType: this.trafficInfo.infoType,       // 
                alertRadius: this.trafficInfo.alertRadius,                      
                alertPath: this.trafficInfo.alertPath,
                alertCategory: this.trafficInfo.alertCategory,
            };

            this.$api.post( url,params,
                response => {
                    if (response.status >= 200 && response.status < 300) {
                        
                        if(response.data.status == 200){
                            this.$message({
                                type: 'success',
                                duration: '1500',
                                message: "",
                                showClose: true
                            });
                            this.$emit('PublishInfo',this.trafficInfo);
                            this.closeMyInfoWindow();
                            this.initSelect();
                            this.initTrafficInof();
                            this.publishLoading = false; 
                        }else if(response.data.status == 500){
                            let msg = response.data.message ? response.data.message : ' !';
                            this.$message({
                                type: 'error',
                                duration: '1500',
                                message: msg,
                                showClose: true
                            });
                            this.publishLoading = false;
                        }
                        
                    } else {                     
                        this.$message({
                            type: 'error',
                            duration: '1500',
                            message: " ",
                            showClose: true
                        }); 
                        this.publishLoading = false;
                    }
                }
            );
        },        
        updateInfo(e){
            this.updateLoading = true;
            this.trafficInfo.datasource = this.select.datasource ? (this.select.datasource.key ? this.select.datasource.key : '') : '';
            this.trafficInfo.frequencyUnit = this.select.frequencyUnit ? (this.select.frequencyUnit.key ? this.select.frequencyUnit.key : '') : '';
            // console.log(this.trafficInfo.alertCategory);
           if(!this.submitForm()) return; 
            let url = 'event/task/update';
            let params = {
                id: this.trafficInfo.id,
                "taskCode": this.trafficInfo.taskCode,
                eventType: this.trafficInfo.eventType,      //
                longitude: this.trafficInfo.longitude,      // 
                latitude: this.trafficInfo.latitude,       // 
                affectRange: this.trafficInfo.affectRange,    // 
                content: this.trafficInfo.content,        // 
                frequency: this.trafficInfo.frequency,      // 
                frequencyUnit: this.trafficInfo.frequencyUnit,      // 
                beginTime: TDate.dateToMs(this.trafficInfo.beginTime),      // 
                endTime: TDate.dateToMs(this.trafficInfo.endTime),     // 
                datasource: this.trafficInfo.datasource,     // 
                sendChannel: this.trafficInfo.sendChannel,         //  4G
                infoType: this.trafficInfo.infoType,       // 
                alertRadius: this.trafficInfo.alertRadius,                      
                alertPath: this.trafficInfo.alertPath,
                alertCategory: this.trafficInfo.alertCategory,
            };

            this.$api.post( url,params,
                response => {
                    if (response.status >= 200 && response.status < 300) {                    
                        
                        if(response.data.status == 200){
                            this.$message({
                                type: 'success',
                                duration: '1500',
                                message: "",
                                showClose: true
                            }); 
                            this.$emit('UpdateInfo',this.trafficInfo);
                            this.updateLoading = false;
                            this.closeMyInfoWindow();
                            this.initSelect();
                            this.initTrafficInof();
                        }else if(response.data.status == 500){
                            let msg = response.data.message ? response.data.message : ' !';
                            this.$message({
                                type: 'error',
                                duration: '1500',
                                message: msg,
                                showClose: true
                            }); 
                            this.updateLoading = false;
                        }
                    } else {                     
                        this.$message({
                            type: 'error',
                            duration: '1500',
                            message: " ",
                            showClose: true
                        });  
                        this.updateLoading = false;
                    }
                }
            );
        },
        destroyInfo(e){ 
            this.invalidLoading = true;
            let url = 'event/task/cancel';
           // console.log(TDate.formatTime())
            let params = {
                id: this.trafficInfo.id,
                "taskCode": this.trafficInfo.taskCode,
                "expirationTime": new Date().getTime(), 
                "status": 2                
            };

            this.$api.post( url,params,
                response => {
                    if (response.status >= 200 && response.status < 300) {
                        
                        if(response.data.status == 200){
                            this.$message({
                                type: 'success',
                                duration: '1500',
                                message: "",
                                showClose: true
                            });  
                            this.$emit('DestroyInfo',this.trafficInfo);
                            this.closeMyInfoWindow(e);
                            this.initSelect();
                            this.initTrafficInof();
                        }else if(response.data.status == 500){
                            let msg = response.data.message ? response.data.message : ' !';
                            this.$message({
                                type: 'error',
                                duration: '1500',
                                message: msg,
                                showClose: true
                            });  
                        }                         
                        this.invalidLoading = false;
                    } else {                     
                        this.$message({
                            type: 'error',
                            duration: '1500',
                            message: " ",
                            showClose: true
                        });   
                        this.invalidLoading = false;
                    }
                }
            );
            // this.$emit('DestroyInfo',this.trafficInfo);
            // this.closeMyInfoWindow(e);
            // this.initSelect();
            // this.initTrafficInof();
        },
        closeInforWindow(e){            
            this.closeMyInfoWindow();
            this.initSelect();
            this.initTrafficInof();
            this.$emit("setPointer",{bool: false, flag: true}); 
            this.updateLoading = false;
            this.publishLoading = false;
            this.invalidLoading = false;
        },
        initDatasourceList(isEdit=false,datasource){
            let url = 'common/queryDictionary';
            let params = {
                parentCode: 'trafficSource',
            };
            this.$api.post( url,params,
                response => {
                    if (response.status >= 200 && response.status < 300) {
                        this.datasourceList = response.data ? response.data : [];
                        if(this.datasourceList.length){
                            
                            if (!isEdit) {
                                this.select.datasource = this.datasourceList[0];
                            }else{
                             
                                for(let item of this.datasourceList){
                                    if(item.key == datasource){
                                        this.select.datasource = item;
                                        break;
                                    }
                                }
                            }
                        }
                    
                    } else {                     
                        this.$message({
                            type: 'error',
                            duration: '1500',
                            message: " ",
                            showClose: true
                        });   
                    }
                }
            );
        },
        initUnintList(isEdit=false,frequencyUnit){
            let url = 'common/queryDictionary';
            let params = {
                parentCode: 'timeUnit',
            };
            this.$api.post( url,params,
                response => {
                    if (response.status >= 200 && response.status < 300) {

                        this.frequencyUnitList = response.data ? response.data : [];
                     
                        if(this.frequencyUnitList.length){
                            if(!isEdit){
                                this.select.frequencyUnit = this.frequencyUnitList[0];
                            }else{
                                for(let item of this.frequencyUnitList){
                                    if(item.key == frequencyUnit){
                                        this.select.frequencyUnit = item;
                                        break;
                                    }
                                }
                            }
                        }                        
                    
                    } else {                     
                        this.$message({
                            type: 'error',
                            duration: '1500',
                            message: " ",
                            showClose: true
                        });   
                    }
                }
            );
        },
        toFixedLen(num,length=7){
            if(num == ''){
                return '';
            }
            if(Object.prototype.toString.call(num) != '[Object Number]'){
                num = new Number(num);
            }    
            return num.toFixed(length);
        },
        initDetail(marker,flag) {
            let url = 'event/task/findDetail';
            let params = {
                taskCode: marker.id,
            };
            this.$api.post( url,params,
                response => {
                    if (response.status >= 200 && response.status < 300) {
                        // console.log("----------------------------");
                        // console.log(response.data);
                        this.trafficInfo.longitude = this.toFixedLen(this.trafficInfo.longitude);
                        this.trafficInfo.latitude = this.toFixedLen(response.data.latitude);
                        this.trafficInfo.id = response.data.id;
                        this.trafficInfo.taskCode = response.data.taskCode;
                        this.trafficInfo.eventName = response.data.eventName;
                        this.trafficInfo.eventType = response.data.eventType;
                        this.trafficInfo.longitude = this.toFixedLen(response.data.longitude);
                        this.trafficInfo.latitude = this.toFixedLen(response.data.latitude);
                        this.trafficInfo.affectRange = response.data.affectRange; 
                        this.trafficInfo.content = response.data.content; 
                        this.trafficInfo.frequency = response.data.frequency; 
                        this.trafficInfo.frequencyUnit = response.data.frequencyUnit; 
                        this.trafficInfo.beginTime = response.data.beginTime; 
                        this.trafficInfo.endTime = response.data.endTime; 
                        this.trafficInfo.datasource = response.data.datasource;     
                        this.trafficInfo.infoType = response.data.infoType;
                        this.trafficInfo.sendChannel = response.data.sendChannel; 
                        if(!flag) {
                            this.trafficInfo.alertPath = response.data.alertPath;   
                            this.select.alertPath = response.data.alertPath;    
                        }
                        this.trafficInfo.alertRadius = response.data.alertRadius;
                        this.trafficInfo.alertCategory = response.data.alertCategory;  
                        const radius = response.data.affectRange;   

                        this.circleLon = response.data.longitude;
                        this.circleLat = response.data.latitude;
                        this.drawBgCircle(response.data.longitude,response.data.latitude,radius);                                            

                        if(response.data.status == 200){                            
                            this.$message({
                                type: 'success',
                                duration: '1500',
                                message: "",
                                showClose: true
                            });   
                        }
                        this.initUnintList(true,this.trafficInfo.frequencyUnit);
                        this.initDatasourceList(true,this.trafficInfo.datasource);
                        
                    } else {                     
                        this.$message({
                            type: 'error',
                            duration: '1500',
                            message: " ",
                            showClose: true
                        });    
                    }
                }
            );
        },

