        //  * @param {number} lon 
        //  * @param {number} lat 
        //  * @param {string} id id
        //  * @param {string} layerId id
        //  * @param {string} carImgUrl 
        //  * @param {Array.<number>} size [22,37]
        //  * @param {number} rotation 90Math.PI/2
        //  * @param {boolean} rotateWithView view
        //  * @param {number} opacity 01
        //  * @param {Array.<number>} offset xy[0,0][0,0]
        //  * @param {number} scale 1
        //  */
        // addImg:function(lon,lat,id,layerId,carImgUrl,size,rotation,rotateWithView,opacity,offset,scale){
        //      let carStyle = new Style({
        //         image:mapInit.generateIcon(carImgUrl||"static/images/warning.png",size||[22,37],rotation||0,rotateWithView||true,opacity||1,offset||[0,0],scale)
        //     });
        //     mapInit.addPoint(lon,lat,id,carStyle,this.getLayerById(layerId));
        // },
        /**
        * 
        *
        * @param {number} lon 
        * @param {number} lat 
        * @param {string} id id
        * @param {string} layerId id
        * @param {string} carImgUrl 
        * @param {Array.<number>} size [22,37]
        * @param {number} rotation 90Math.PI/2
        * @param {boolean} rotateWithView view
        * @param {number} opacity 01
        * @param {Array.<number>} offset xy[0,0][0,0]
        * @param {number} scale 1
        * @param {Array.<number>} anchor Anchor. Default value is the icon center.
        */
        addImg:function(lon,lat,id,layerId,carImgUrl,size,rotation,rotateWithView,opacity,offset,scale,anchor){
            let carStyle = new Style({
                image:mapInit.generateIcon(carImgUrl||"../../static/assets/images/geolocation_marker_heading.png",size||[22,37],rotation||0,rotateWithView||true,opacity||1,offset||[0,0],scale,anchor)
            });
            mapInit.addPoint(lon,lat,id,carStyle,this.getLayerById(layerId));
        },       

        /**                                                                                                                                                                                                                                                                                                                                                                                                                                                                             
         * 
         * 
         * @param {number} lon 
         * @param {number} lat 
         * @param {string} id id
         * @param {string} layerId id
         * 
         * @param {module:ol/style/Fill~Fill} fill generateFill
         * @param {number} points 
         * @param {number} radius 
         * @param {number} radius1  
         * @param {number} radius2  
         * @param {number} angle 
         * @param {module:ol/style/Stroke~Stroke} stroke generateStroke
         * @param {number} rotation 
         * @param {boolean} rotateWithView view
        */
        addRegularShapePoint:function(lon,lat,id,layerId,fill, points, radius, radius1, radius2, angle, stroke, rotation, rotateWithView){
             let carStyle = new Style({
                image:mapInit.generateRegularShape(fill, points, radius, radius1, radius2, angle, stroke, rotation, rotateWithView)
            });
            mapInit.addPoint(lon,lat,id,carStyle,this.getLayerById(layerId));
        },
        /**
         * 
         * @param {number} lon 
         * @param {number} lat 
         * @param {string} id id
         * @param {string} layerId id
         * 
         * @param {number} radius  5
         * @param {module:ol/color~Color | module:ol/colorlike~ColorLike} fillColor red'black''red''green''white''#4271AE' 
         * @param {module:ol/color~Color | module:ol/colorlike~ColorLike} strokeColor green'black''red''green''white''#4271AE'
         * @param {number} StrokeWidth  3
         */
        addNormalPoint:function(lon,lat,id,layerId,radius,fillColor,strokeColor,StrokeWidth){
            let image = new CircleStyle({
                radius: radius||5,
                fill: new Fill({
                    color: fillColor||"red"
                }),
                stroke: new Stroke({
                    color: strokeColor||"green",
                    width: StrokeWidth||3
                })
            });
            let carStyle = new Style({
                image:image
            });
            mapInit.addPoint(lon,lat,id,carStyle,this.getLayerById(layerId));
        },

        /**
         * 
         * 
         * @param {number} lon 
         * @param {number} lat 
         * @param {string} id id
         * @param {string} layerId id
         * @param {string} text 
         * @param {string} font 'bold 14px "Open Sans", "Arial Unicode MS", "sans-serif"'
         * @param {string} placement linepoint,line
         * @param {number} textOffsetX  x0
         * @param {number} textOffsetY  y0
         * @param {string} fillColor white
         * @param {module:ol/style/Stroke~Stroke} stroke 
         * @param {string} backgroundFillColor black
         * @param {module:ol/style/Stroke~Stroke} backgroundStroke 
         * @param {Array.<number>} padding [0, 0, 0, 0]
         * 
         * @param {boolean} withImg 
         * @param {string} imgUrl 
         * @param {Array.<number>} imgSize [22,37]
         * @param {Array.<number>} imgOffset xy[0,0][0,0]
         * @param {number} scale 1
         */
        addText:function(lon,lat,id,layerId,text, font, placement, textOffsetX, textOffsetY, fillColor, stroke, backgroundFillColor, backgroundStroke, padding,withImg,imgUrl,imgSize,imgOffset,scale){
             let textStyle = null;
             if(withImg){
                textStyle = new Style({
                    image:mapInit.generateIcon(imgUrl||"../../static/assets/images/geolocation_marker_heading.png",imgSize||[22,37],0,true,1,imgOffset||[0,0],scale),
                    text:mapInit.generateText(text, font, placement, textOffsetX, textOffsetY, fillColor, stroke, backgroundFillColor, backgroundStroke, padding||[0, 0, 0, 0]),
                });
             }else{
                textStyle = new Style({
                    text:mapInit.generateText(text, font, placement, textOffsetX, textOffsetY, fillColor, stroke, backgroundFillColor, backgroundStroke, padding||[0, 0, 0, 0]),
                });
             }
             
            mapInit.addPoint(lon,lat,id,textStyle,this.getLayerById(layerId));
        },

        /**
         * 
         * @param {Array.<Array.<number>>} coordinates [[112,39],[113,40]]
         * @param {string} id ID
         * @param {module:ol/color~Color | module:ol/colorlike~ColorLike} color 'black''red''green''white''#4271AE' [red, green, blue, alpha]
         * @param {string} lineCap butt, round, or square.
         * @param {string} lineJoin bevel, round, or miter.
         * @param {Array.<number>} lineDash [5,5]
         * @param {number} lineDashOffset 0
         * @param {number} miterLimit 10
         * @param {number} width 
         * @param {string} layerId id
         */
        addLineString:function(coordinates, id, color, lineCap, lineJoin, lineDash, lineDashOffset, miterLimit, width, layerId) {
            let lineStyle = new Style({
                stroke:mapInit.generateStroke(color, lineCap, lineJoin, lineDash, lineDashOffset, miterLimit, width)
            });
            mapInit.addLineString(coordinates, id, lineStyle, this.getLayerById(layerId));            
        },
        /**
         * 
         *  @param {number} lon 
         *  @param {number} lat 
         *  @param {number} radius 
         *  @param {any} id 
         *  @param {module:ol/color~Color | module:ol/colorlike~ColorLike} fillColor 'black''red''green''white''#4271AE' [red, green, blue, alpha]
         *  @param {module:ol/color~Color | module:ol/colorlike~ColorLike} strokeColor 'black''red''green''white''#4271AE' [red, green, blue, alpha]
         *  @param {string} lineCap butt, round, or square.
         *  @param {string} lineJoin bevel, round, or miter.
         *  @param {Array.<number>} lineDash [5,5]
         *  @param {number} lineDashOffset 0
         *  @param {number} miterLimit 10
         *  @param {number} width 
         *  @param {module:ol/Layer} layer OpenlayersLayer
         */
        addCircle:function(lon, lat, radius, id,fillColor,strokeColor, lineCap, lineJoin, lineDash, lineDashOffset, miterLimit, width, layerId) {
            let circleStyle = new Style({
                stroke:mapInit.generateStroke(strokeColor, lineCap, lineJoin, lineDash, lineDashOffset, miterLimit, width),
                fill:mapInit.generateFill(fillColor)
            });
            mapInit.addCircle(lon, lat,radius, id, circleStyle, this.getLayerById(layerId));
        },
        /**
         * 
         * @param {Array.<Array.<Array.<module:ol/coordinate~Coordinate>>>} coords 
         * @param {string} id ID
         * @param {module:ol/color~Color | module:ol/colorlike~ColorLike} fillColor 'black''red''green''white''#4271AE' [red, green, blue, alpha]
         * @param {module:ol/color~Color | module:ol/colorlike~ColorLike} strokeColor 'black''red''green''white''#4271AE' [red, green, blue, alpha]
         * @param {string} lineCap butt, round, or square.
         * @param {string} lineJoin bevel, round, or miter.
         * @param {Array.<number>} lineDash [5,5]
         * @param {number} lineDashOffset 0
         * @param {number} miterLimit 10
         * @param {number} width 
         * @param {string} layerId id
         */
        addMultiPolygon:function(coords, id, fillColor,strokeColor, lineCap, lineJoin, lineDash, lineDashOffset, miterLimit, width, layerId) {
            let polygonStyle = new Style({
                stroke:mapInit.generateStroke(strokeColor, lineCap, lineJoin, lineDash, lineDashOffset, miterLimit, width),
                fill:mapInit.generateFill(fillColor)
            });
            mapInit.addMultiPolygon(coords, id, polygonStyle, this.getLayerById(layerId));
        },
        /**
         * 
         * @param {Array.<Array.<number>>} coordinates [[x1,y1],[x2,y2],[x3,y3]]
         * @param {*} id id
         * 
         * @param {module:ol/color~Color | module:ol/colorlike~ColorLike} color 'black''red''green''white''#4271AE'
         * @param {string} lineCap butt, round, or square.
         * @param {string} lineJoin bevel, round, or miter.
         * @param {Array.<number>} lineDash [5,5]
         * @param {number} lineDashOffset 0
         * @param {number} miterLimit 10
         * @param {number} width 
         * 
         * @param {string} tailImgUrl 
         * @param {string} headImgUrl 
         * @param {string} layerId id
         */
        addArrow:function(coordinates, id, color, lineCap, lineJoin, lineDash, lineDashOffset, miterLimit, width, tailImgUrl, headImgUrl, layerId) {
            let lineStyle = new Style({
                stroke:mapInit.generateStroke(color, lineCap, lineJoin, lineDash, lineDashOffset, miterLimit, width)
            });
            mapInit.addArrow(coordinates, id, lineStyle, tailImgUrl||"../../static/assets/images/geolocation_marker.png", headImgUrl||"../../static/assets/images/geolocation_marker_heading.png", this.getLayerById(layerId));
        },
        /**
         * 
         * @param {number} lon 
         * @param {number} lat  
         */
        centerAt:function(lon,lat){
            this.$data.map.getView().setCenter([lon,lat]);
        },
        /**
         * 
         * 
         * @param {number} level 019
         */
        zoomTo:function(level){
            this.$data.map.getView().setZoom(level);
        },
        /**
         * 
         */
        getCurrentExtent:function(){
            return this.$data.map.getView().calculateExtent(this.$data.map.getSize());
        },
        /**
         * 
         */
        getCurrentLevel:function(){
            let z = parseInt(this.$data.map.getView().getZoom());
            return z;
        },
        /**
         * 
         * @param {*} id id
         * @param {*} layerId ID
         */
        removeFeature:function(id, layerId) {
            let source = this.getLayerById(layerId).getSource();
            let f = source.getFeatureById(id);
            if (f != null) {
                source.removeFeature(f);
            }
        },
        /**
         * 
         * @param {*} layerId ID
         */
        removeAllFeature:function(layerId) {
            let source = this.getLayerById(layerId).getSource();
            if (source != null) {
                source.clear();
            }
        },
        // 
        storkeLine(tracks) {
            
        },
        /**
         * wms
         * 
         * @param {String} id id
         * @param {String} url wms
         * @param {String} layers wms
         * @param {String} styles wms
         * @param {number} opacity wms0~1,10
         * @param {boolean} visble  wmstrue
         * @param {String} version  wms
         */
        addWms:function(id,url,layers,styles,opacity,visble,version){
            mapInit.addWms(id,url,layers,styles,opacity,visble,version,this.$data.map)
        },
        // 
        translateCallback(data,color){

        },
        pageResize(){
            const borwserHeight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;
            this.pageHeight = borwserHeight - 64;        
        },
        resize:function(size) {
            this.$data.map.setSize(size);
        }
    },
    created(){
        this.initUnintList();
        // this.initDatasourceList();
    },
    mounted(){
        this.pageResize();
        // window.onresize = () => {
        //     // this.pageResize();
        // }
        this.initMap();
        this.$emit("MapInitComplete",this);
        //
        // setTimeout(() => {
        //     this.initMap();
        //     this.$emit("MapInitComplete",this);

            
        // }, 100);
    },
    destroyed(){

    }
}
</script>
<style scoped>

    #map {
        width: 100%;
        height: 100%;
        overflow: hidden;
        margin:0;
        font-family:"";

        user-select: none;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
    }
    .map:-moz-full-screen {
        height: 100%;
    }
    .map:-webkit-full-screen {
        height: 100%;
    }
    .map:-ms-fullscreen {
        height: 100%;
    }
    .map:fullscreen {
        height: 100%;
    }
    .ol-rotate {
        top: 3em;
    }

    .ol-popup {
        position: absolute;

        /* background-color: white; */
        background-color: #333333;
        color: #fff;

        -webkit-filter: drop-shadow(0 1px 4px rgba(0,0,0,0.2));
        filter: drop-shadow(0 1px 4px rgba(0,0,0,0.2));
        /* padding: 15px; */
        /* border-radius: 10px; */
        border: 1px solid #cccccc;
        bottom: 12px;
        left: -50px;
        min-width: 280px;
    }
    .ol-popup:after, .ol-popup:before {
        top: 100%;
        border: solid transparent;
        content: " ";
        height: 0;
        width: 0;
        position: absolute;
        pointer-events: none;
    }
    .ol-popup:after {
        /* border-top-color: white; */
        border-top-color: #413d3d;
        border-width: 10px;
        left: 48px;
        margin-left: -10px;
    }
    .ol-popup:before {
        /* border-top-color: #cccccc; */
        border-top-color: #413d3d;
        border-width: 11px;
        left: 48px;
        margin-left: -11px;
    }
    .ol-popup-closer {
        text-decoration: none;
        position: absolute;
        top: 10px;
        right: 10px;
        /* top: 18px;
        right: 18px; */
        font-size: 16px;
        color: #fff;
        /* color: #F59307; */
        cursor: pointer;
        z-index: 9999;
    }
    .ol-popup-closer:hover{
        color: rgb(224, 217, 205);
        /* color: rgb(238, 195, 131); */
    }
    .ol-popup-closer:after {
        content: "";
    }
   

    .yk-pointer-normal{
        cursor: default;
        z-index: 3;
    }

    .yk-close-btn{
        top: 13px;
        right: 10px;
        position: absolute;
        width: 12px;
        cursor: pointer;
        z-index: 2001;
    }
    .yk-close-btn:hover{
        opacity: 0.6;
    }

    .yk-unit{
        width: 30px;
        display: inline-block;
        text-align: center;
    }

    .el-textarea__inner{
        border: 0px!important;
        background: rgb(102, 102, 102)!important;
        border: 0px!important;
        border-radius: 0px!important;
        color: #fff!important;

    }

    .el-select-dropdown__list{
        padding: 0px!important;
        height: 28px!important;
        line-height: 28px!important;
    }

    .yk-btn-tooltip {
        position: absolute;
        right: 50px;
        top: 0;
        &:hover {
            opacity: 1 !important;
        }
    }

</style>
<style lang="scss">
.path-view-popover {
    max-width: 256px;
    border: none;
    color: #999;
    // max-height: 200px;
    // overflow-y: auto;
}
.demo-ruleForm {
    .el-form-item__content {
        width: 240px;
        .el-input__inner {
            box-sizing: border-box;
            width: 100%;
        }
        .el-date-editor.el-input, .el-date-editor.el-input__inner {
            width: 100%;
        }
        &.el-form--inline {
            .el-input__inner {
                box-sizing: border-box;
                width: 100%;
            }
            .el-date-editor.el-input, .el-date-editor.el-input__inner {
                width: 100%;
            }
            // .el-date-editor--datetimerange.el-input, .el-date-editor--datetimerange.el-input__inner {
            //  width: 350px;
            // }
        }
    }
    .el-input-number {
        .el-input-number__increase, .el-input-number__decrease {
            border-radius: 0!important;
        }
    }
}
</style>



