/**
* ICON:ol/style/Icon
* @param {string} src 
* @param {module:ol/size~Size} size [22,22]
* @param {number} rotation ,90Math.PI/2
* @param {boolean} rotateWithView viewfalse
* @param {number} opacity 0-1
* @param {Array.<number>} offset [0,0]
* @param {number} scale 1
* @param {Array.<number>} anchor Anchor. Default value is the icon center.
*/
export function generateIcon(src, size, rotation, rotateWithView, opacity, offset, scale,anchor) {
    // console.log(src);
    let icon = new Icon({
    src: src || "@/assets/images/geolocation_marker.png",
    size: size || [22, 22],
    rotation: rotation || 0,
    rotateWithView: rotateWithView || false,
    opacity: opacity || 1,
    offset: offset || [0, 0],
    scale: scale || 1,
    anchor:anchor||[0.5,0.5]
    });
    return icon;
    }

/**
 * ICON:ol/style/Text
 * @param {string} text 
 * @param {string} font 'bold 14px "Open Sans", "Arial Unicode MS", "sans-serif"'
 * @param {string} placement line
 * @param {number} offsetX  x0
 * @param {number} offsetY  y0
 * @param {string} fillColor white
 * @param {module:ol/style/Stroke~Stroke} stroke 
 * @param {string} backgroundFillColor black
 * @param {module:ol/style/Stroke~Stroke} backgroundStroke 
 * @param {Array.<number>} padding [0, 0, 0, 0]
 */
export function generateText(text, font, placement, offsetX, offsetY, fillColor, stroke, backgroundFillColor, backgroundStroke, padding) {
    console.log(text);
    let textStyle = new Text({
        text: text,
        font: font || 'bold 14px "Open Sans", "Arial Unicode MS", "sans-serif"',
        placement: placement || 'line',
        offsetX: offsetX,
        offsetY: offsetY,
        fill: new Fill({
            color: fillColor || 'white'
        }),
        backgroundFill: new Fill({
            color: backgroundFillColor || 'black'
        }),
        stroke: stroke,
        backgroundStroke: backgroundStroke,
        padding: padding || [0, 0, 0, 0]
    });
    return textStyle;
}

/**
 * 
 * @param {module:ol/style/Fill~Fill} fill generateFill
 * @param {number} points 
 * @param {number} radius 
 * @param {number} radius1  
 * @param {number} radius2  
 * @param {number} angle 
 * @param {module:ol/style/Stroke~Stroke} stroke generateStroke
 * @param {number} rotation 
 * @param {boolean} rotateWithView view
 */
export function generateRegularShape(fill, points, radius, radius1, radius2, angle, stroke, rotation, rotateWithView) {
    let regularShape = new RegularShape({
        fill: fill,
        points: points,
        radius: radius || 5,
        radius1: radius1,
        radius2: radius2,
        angle: angle || 0,
        stroke: stroke,
        rotation: rotation || 0,
        rotateWithView: rotateWithView || false
    });
    return regularShape;
}


/**
 * 
 * @param {	module:ol/color~Color | module:ol/colorlike~ColorLike} color 'black''red''green''white''#4271AE' [red, green, blue, alpha]
 */
export function generateFill(color) {
    let fill = new Fill({
        color: color || 'red'
    });
    return fill;
}


/**
 * 
 * @param {module:ol/color~Color | module:ol/colorlike~ColorLike} color 'black''red''green''white''#4271AE'
 * @param {string} lineCap butt, round, or square.
 * @param {string} lineJoin bevel, round, or miter.
 * @param {Array.<number>} lineDash [5,5]
 * @param {number} lineDashOffset 0
 * @param {number} miterLimit 10
 * @param {number} width 
 */
export function generateStroke(color, lineCap, lineJoin, lineDash, lineDashOffset, miterLimit, width) {
    let stroke = new Stroke({
        color: color || 'red',
        lineCap: lineCap || 'round',
        lineJoin: lineJoin || 'round',
        lineDash: lineDash,
        lineDashOffset: lineDashOffset || 0,
        miterLimit: miterLimit || 10,
        width: width
    });
    return stroke;
}


